body {
    position: relative;
    margin: 0;
    padding: 0;
    background-color: black;
}
main {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    height: calc(100vh - 56px);
    overflow: hidden;
}
footer {
    position: absolute;
    bottom: 0;
    transform: translate(0, 100%);
    width: 100%; height: 100%;
    background: rgb(33,37,41);
    color: white;
    height: 15rem;
}





.slider button {
    z-index: 1000;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    width: 40px;
    height: 40px;
    background: black;
    border: 1px solid white;
    border-radius: 10px;
    text-decoration: none;
}
.slider button:hover {
    background: rgb(33,37,41);
}
.slider .slider-prev {
    left: 30px;

}
.slider .slider-next {
    right: 30px;
}
.slider .slider-box {
    display: flex;
    height: 50rem;
    padding-left: calc(50vw - 15rem);
    transition: .5s;
}   
.slider .card {
    text-align: center;
    margin: 0 3rem;
    width: 30rem;
    height: 100%;
    background: black;
}
.slider .card:nth-child(1){
    margin-left: 0;
}
.slider .card img {
    object-fit: cover;
    height: 45rem;
    border-radius: 10px;
}
.slider .card h3 {
    margin-top: 10px;
    color: white;
}




.credits {
    color: white;
    margin: 0 auto;
    text-align: center;
    overflow: visible;
    animation-duration: 30s;
    animation-name: slidein;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@keyframes slidein {
    from {
        transform: translate(0, calc(50% + 50vh));
    }

    to {
        transform: translate(0, calc(-50% - 50vh));
    }
}
.credits > div {
    margin: 15rem 0;
}
.credits > div h1 {
    margin-bottom: 20px;
}







footer ul{
    list-style: none;
}

footer ul h5 {
    color: rgba(255, 255, 255, 0.5); 
}

